@font-face
    font-family: 'Roboto' /* Font family name */
    src: url('../../assets/fonts/Roboto.ttf') format('truetype')
    font-weight: 400 /* Font weight */
    font-style: normal /* Font style (normal, italic) */

@font-face
    font-family: 'Roboto' /* Font family name */
    src: url('../../assets/fonts/Roboto-Italic.ttf') format('truetype')
    font-weight: 400 /* Font weight */
    font-style: italic /* Font style (normal, italic) */

.appBar
    z-index: 100
    height: 70px

.toolbar
    display: flex
    align-items: center
    height: 100%
    gap: 10px
    justify-content: flex-start
    background-color: #00838F

.searchInput
    background-color: #F2F2F2
    border-radius: 5px
    width: 65%
    height: 40px
    margin-left: 30px
    padding-left: 15px
    font-family: 'Roboto', sans-serif
    font-style: italic
    font-size: 14px

.searchButton
    font-family: 'Roboto', sans-serif
    font-style: normal
    line-height: 26px
    letter-spacing: 0.46px
    margin-left: 10px
    width: 100px

.footer
    z-index: 100
    background-color: #4F4F4F
    width: 100%
    height: 35px